import stampit from 'stampit';
import { readFile } from 'fs';
import { promisify } from 'util';
import Resolver from "./Resolver.js";
import { isFileSystemPath, toFileSystemPath } from "../../util/url.js";
import { ResolverError } from "../../util/errors/index.js";
const FileResolver = stampit(Resolver, {
  init() {
    this.name = 'file';
  },

  methods: {
    canRead(file) {
      return isFileSystemPath(file.uri);
    },

    async read(file) {
      const fileSystemPath = toFileSystemPath(file.uri);

      try {
        return await promisify(readFile)(fileSystemPath);
      } catch (error) {
        throw new ResolverError(`Error opening file "${file.uri}"`, error);
      }
    }

  }
});
export default FileResolver;